import * as React from "react"
import { Link } from 'react-router-dom'
// import  { navIcon } from "./icons"
import "./nav.css";

function NavLink(props:{selfPath:string}){
    return (
        <li>
            <Link to={props.selfPath}>
                123
            </Link>
        </li>
    )
}
class Nav extends React.Component {
    public render(){
        return(
            <nav className="nav-bar">
                <ul>
                   <NavLink selfPath="/"/>
                   <NavLink selfPath="/rank"/>
                   <NavLink selfPath="/find"/>
                   <NavLink selfPath="/strategy"/>
                   <NavLink selfPath="/my"/>
                </ul>
            </nav>
        )
    }
}
export default Nav;


